import React, {useState,  useEffect} from 'react'
import axios from 'axios'
import './App.css';

function App() {
  const [people, setPeople] = useState([])
  useEffect(() => {
      axios({
        method:'get',
        url:'http://127.0.0.1/api/test-api'
      }).then(res => {
          setPeople(res.data)
      })
     } ,[])

  return (
    <div className="App">
      <header className="App-header">
        <h1> Hello form REACT</h1>
        <ul>
        {people.map(p => (<li key ={p.id}>{p.name}</li>))}
        </ul>
      </header>
    </div>
  );
}

export default App;
